      <!-- 项目优势 -->
      <view class="section">
        <text class="section-title">⛳️ 项目优势</text>
        
        <!-- 动态研学与传统研学对比 -->
        <view class="subsection">
          <text class="subsection-title">一、动态研学与传统研学差异化对比</text>
          <view class="table-container">
            <view class="compare-table">
              <view class="table-header">
                <view class="table-cell">维度</view>
                <view class="table-cell">传统研学</view>
                <view class="table-cell">动态研学</view>
              </view>
              <view class="table-row">
                <view class="table-cell">内容设计</view>
                <view class="table-cell">学科分割</view>
                <view class="table-cell">学科整合</view>
              </view>
              <view class="table-row">
                <view class="table-cell">学生角色</view>
                <view class="table-cell">被动接受者</view>
                <view class="table-cell">主动参与者、合作者</view>
              </view>
              <view class="table-row">
                <view class="table-cell">知识获取</view>
                <view class="table-cell">系统性强，适合打基础</view>
                <view class="table-cell">实用性强，适合培养综合能力</view>
              </view>
              <view class="table-row">
                <view class="table-cell">流程</view>
                <view class="table-cell">固定</view>
                <view class="table-cell">灵活设计</view>
              </view>
              <view class="table-row">
                <view class="table-cell">学习形式</view>
                <view class="table-cell">固定流程，单向传递，静态场景</view>
                <view class="table-cell">实用性强，适合培养综合能力</view>
              </view>
              <view class="table-row">
                <view class="table-cell">评价方式</view>
                <view class="table-cell">标准化标准化指标为主</view>
                <view class="table-cell">注重过程性评价、学生自评与反思</view>
              </view>
            </view>
          </view>
        </view>

        <!-- 动态研学模式 -->
        <view class="subsection">
          <text class="subsection-title">二、动态研学模式</text>
          <view class="paragraph">
            动态研学模式以拟定主题为核心，围绕学科目标和项目目标设计任务，通过监控与反馈机制，结合一对一教师、专家等指导，培养学生综合能力。
          </view>
          <view class="paragraph">
            阶段包括准备阶段（分工、知识前测等）、实施过程（设计、制作、转换等），学习地点涵盖大成藏、博物馆、企业公司等社会资源。
          </view>
          <view class="paragraph">
            动态研学学强调动态调整、实时完善，注重调动参与者积极性，通过复盘总结归纳不足并改善。
          </view>
          
          <!-- 图片占位 -->
          <view class="img-placeholder">
            <image src="https://llzm-lixiaobao.oss-cn-beijing.aliyuncs.com/%E9%A6%96%E9%A1%B5/%E5%8A%A8%E6%80%81%E7%A0%94%E5%AD%A6%E6%A8%A1%E5%BC%8F.png" mode="widthFix" 
                  @error="handleImgError"></image>
            <text class="img-desc">动态研学模式流程图</text>
          </view>
        </view>

        <!-- 项目数据支撑 -->
        <view class="subsection">
          <text class="subsection-title">三、数据支撑</text>
          <view class="paragraph">
            <text class="highlight">项目周期：</text>累计开展3期(第一期至第三期)
          </view>
          <view class="paragraph">
            <text class="highlight">参与规模：</text>总人数近300人(从第1期24人增长至第3期)
          </view>
          <view class="paragraph">
            <text class="highlight">能力提升：</text>中学生100%覆盖历史探究、数字创作、跨学科应用能力
          </view>
          <view class="paragraph">
            <text class="highlight">学术成果：</text>3篇论文(例:《浅谈军工企业改革-以重庆国营建设机床厂为例》)
          </view>
          <view class="paragraph">
            <text class="highlight">宣传成果：</text>近20部视频(《家书》被建川博物馆采用)，3天浏览量约9000条
          </view>
          <view class="img-placeholder">
            <image src="https://llzm-lixiaobao.oss-cn-beijing.aliyuncs.com/%E9%A6%96%E9%A1%B5/%E6%95%B0%E6%8D%AE%E6%94%AF%E6%92%91%E6%B1%87%E6%80%BB.png" 
                  @error="handleImgError"></image>
            <text class="img-desc">数据支撑汇总</text>
          </view>
        </view>

        <!-- 双线融合模式 -->
        <view class="subsection">
          <text class="subsection-title">四、双线融合的社会资源+学校资源结合模式</text>
          <view class="paragraph">
          本项目整合社会资源与学校资源，将大学（重庆师范大学）与中学（育才中学）、博物馆（建川博物馆）、社会企业联合（长安厂等），打破了传统教育或服务的单一性，构建协同育人和资源共享的研学模式，实现了学校社会资源的互补，弥补了学校在实践场景、前沿技术、社会化服务的不足，同时为社会培养综合性人才贡献力量。
          </view>
          <view class="paragraph">
            <text class="highlight">对大学：</text>突破资源边界，响应第二课堂制度，提升学生实践能力与综合素质
          </view>
          <view class="paragraph">
            <text class="highlight">对中学：</text>拓展资源，优化成本，提升教学质量和学生综合素质
          </view>
          <view class="paragraph">
            <text class="highlight">对企业：</text>扩大知名度与影响力，实现社会责任与经济效益双赢
          </view>
        </view>

        <!-- 大先生带小先生机制 -->
        <view class="subsection">
          <text class="subsection-title">五、大先生带小先生机制</text>
          <view class="paragraph">
              “大先生牵手小先生”模式是一种融合教育者（“大先生”共学社区）与青少年学生（“小先生”）协同参与教育与社会治理的创新实践。其核心在于通过代际互动、资源共享和角色转换，构建“教学相长”的育人生态。
          </view>
          <view class="paragraph">
              这是一项完善的共学体系，大学生与中学生共学，打破传统单向传授模式，主体双方既是学习者又是实践导师，大学生（大先生）则从知识传授者转变为资源整合者与成长引导者。 “大先生”中的专业领域的导师和本科在读大学生提供贡献学术方法论与跨学科视野等专业支持，而“小先生”则充分发挥创新实践力，以具体实践反哺教育场景，形成“理论+实践”闭环。“大先生”通过指导“小先生”提升实践教学能力，双方共同合作，增强责任感与领导力。
          </view>
          <view class="paragraph">
              另外，在双方无法解决问题时，专业指导教师还会作为协调型“大先生”为我们两方主体提供帮助，比如搭建资源网络（对接企业、政策智库）、设计协作工具包（分享专业书籍），分享跨学段育人模式创新经验。帮助大中学生突破难关。
          </view>
          <view class="paragraph">
              事实上这个共学体系相当具有可行性，在三期项目的实践中，我们也对这个机制进行了不断完善。在2023年第一期中，作为首次实践，我们尝试大学生与中学生进行一对一对接，卓有成效，但是因为参与人员较少，未完善筛选机制，大先生和小先生相性较低，给出的成果有限，效率较低。于是在2024年第二期中，我们进行了一对多的扩展，小先生成员涵盖初中高中，一名大学生带多名小先生进行研学，但是大学生管理能力有限，小先生研学时间难以统一，使得组织相对混乱，任务完成度并没有那么可观。最后在2025年第三期，我们在回归一对一的基础上进行了升华，提前做好问卷确定双方意向深度定制，双方在有共同意愿的情况下同出一气，一齐探讨研学，遇见痛点则与指导老师一同解决。共学体系的优势得到了充分发挥，既延续“应试补救与素养赋能”的核心目标，更通过代际共治的创新，为“大思政课”建设提供可复制的生态范本。
          </view>
          <view class="paragraph">
              创建这个机制时，我们的核心目标是“应试补救”与“素养赋能”。针对初中生，我们设计独特的环节与研学项目，弥补中考弱化后的兴趣缺失，提前培养中学生历史素养。而对于高中生，我们则试图提供“新高考历史选科衔接课”，聚焦史料分析、论文写作等高考薄弱环节，方便更好地与大学相关专业接轨。这是一项独特的创新，也是一次全新的探索。
          </view>
        </view>


        <!-- 服务对象 -->
        <view class="subsection">
          <text class="subsection-title">六、服务对象和内容</text>
          <view class="paragraph">
              我们的核心价值主张在于构建一个连接多元教育主体、以跨学科项目研学为驱动的创新教育生态。深入理解并满足不同用户群体的独特需求，是这一生态得以蓬勃发展的基石。以下是对我们核心服务对象的详细分析：
          </view>
          <view class="paragraph">
            <text class="highlight">（一）中学生 求知探索与能力成长的直接受益者：</text>
            <view class="paragraph">
            中学生正处于知识体系构建、兴趣探索和关键能力培养的黄金时期。他们渴望新颖、互动性强、脱离传统课堂灌输模式的学习体验。面对升学压力和信息过载，他们需要既能激发内在动力、又能有效提升综合素质的学习路径。我们的服务精准锚定这一需求痛点：
            </view>
            <view class="paragraph">
            体验驱动：“大学生带中学生”的独特模式，巧妙地利用了中学生对“榜样”的天然认同感和亲近感。大学生导师兼具知识传授者和同龄引领者的双重身份，能迅速打破代际沟通壁垒，营造出轻松、平等、有趣的研学氛围，显著提升中学生的参与意愿和投入度。
            </view>
            <view class="paragraph">
            能力导向：项目研学超越单一学科知识传授，核心在于培养批判性思维、团队协作、解决问题、沟通表达等未来社会必备的核心素养。通过精心设计的跨学科项目（如“洞见历史”），学生在真实或模拟的情境中主动探究、实践应用，实现知识的内化与能力的跃升。
            </view>
            <view class="paragraph">
            个性赋能：我们认识到每个学生都是独特的个体。因此，服务不仅提供丰富的跨学科学习资源和沉浸式项目，更配套个性化的学习建议。基于项目参与表现和科学评估，为学生提供针对性的能力发展路径规划和资源推荐，助力其发现潜能、弥补短板，实现更全面的个性化成长。
            </view>
            <view class="paragraph">
            成果可见：科学的学习效果评估体系，不仅服务于教学优化，更能让中学生及其家长清晰看到参与项目的实际收获与进步，增强学习成就感和持续投入的动力。
            </view>
          </view>
          <view class="paragraph">
            <text class="highlight">（二）中学教师  跨学科教学的赋能者与伙伴 ：</text>提供跨学科课程设计支持、教学资源库、效果评估工具，减轻教学负担，提升专业能力。
            <view class="paragraph">
                中学教师是教育创新的关键执行者，但在推动跨学科教学实践中常面临巨大挑战。课程设计难度高、跨学科资源整合耗时费力、缺乏有效的评估工具、以及教学创新带来的不确定性，都成为阻碍。
            </view>
            <view class="paragraph">
                减负增效：我们提供的跨学科课程设计支持（如围绕“洞见历史”等主题的成熟框架）和精准匹配的优质教学资源库，能显著减轻教师自主开发课程的前期负担，让他们能将宝贵精力更多投入到教学实施与学生互动中。
            </view>
            <view class="paragraph">
                专业提升：平台不仅提供资源，更是一个专业发展的支持系统。通过接触前沿的跨学科理念、项目设计方法和配套的教学策略指导，教师能够提升自身的跨学科教学能力和创新思维，从而更自信、更有效地开展教学实践。
            </view>
            <view class="paragraph">
                效果保障：提供的教学效果评估工具和方法，帮助教师摆脱经验主义，科学地衡量跨学科教学对学生知识掌握、能力提升和素养发展的实际影响，为教学反思、优化和成果展示提供数据支撑。
            </view>
            <view class="paragraph">
                问题解决：平台旨在成为教师应对跨学科教学实际难题的“智库”。无论是寻找特定主题的资源、设计有效的探究任务，还是评估复杂的跨学科技能，系统都能提供针对性的解决方案和支持，助力教师克服教学过程中的具体障碍。
            </view>
          </view>
          <view class="paragraph">
            <text class="highlight">（三）学校 教育体系升级与品牌塑造的推动者：</text>
            <view class="paragraph">
                学校（涵盖中小学及高校）作为教育的主阵地，面临着提升教育质量、打造特色、增强竞争力、响应国家教育政策导向（如素质教育、大中小学一体化）等多重压力。
            </view>
            <view class="paragraph">
                体系构建：我们提供的不是零散的项目，而是协助学校系统性构建符合其自身定位和发展目标的跨学科研学体系。这包括顶层设计、课程资源整合、师资培训、实施流程、评估机制等全方位支持，确保研学活动可持续、高质量地融入学校整体教育规划。
            </view>
            <view class="paragraph">
                一体化实践：尤其强调“大学生带中学生”模式在促进“大中小学思政教育一体化”中的独特价值。通过高校与中学的深度联动（高校提供优秀学生导师，中学提供实践场景），不仅丰富了中学生的学习体验，也为高校学生提供了宝贵的实践锻炼和社会服务机会，形成良性的教育生态循环，切实响应国家教育战略。
            </view>
            <view class="paragraph">
                模式优化与竞争力提升：在传统教育模式面临挑战的今天，引入创新的跨学科项目研学是学校优化教育模式、提升吸引力的关键举措。以“洞见历史”等特色项目为例，学校能够快速落地具有辨识度的研学活动，显著增强其在区域内的教育竞争力和差异化优势。
            </view>
            <view class="paragraph">
                社会影响力：成功的、有深度的特色研学项目是学校展示其教育创新成果和育人成效的绝佳窗口。通过组织此类活动并展示学生成果，学校能有效提升其社会声誉和影响力，吸引优质生源，获得更广泛的社会认可和支持。
            </view>
            
          </view>
          <view class="paragraph">
            <text class="highlight">（四）教育机构：</text>
            <view class="paragraph">
                在竞争日益激烈的教育市场中，各类创新教育机构（如研学旅行机构、营地教育机构、STEAM教育机构、素质教育平台等）亟需突破同质化竞争，打造核心优势。
            </view>
            <view class="paragraph">
                解决方案定制：我们深刻理解不同机构在定位、客群、资源禀赋上的差异。因此，服务核心在于提供高度个性化的研学解决方案，而非标准化产品。根据机构的具体需求（如目标客群年龄、地域特色、核心能力培养方向等），共同设计或深度定制独特的跨学科主题学习活动和特色课程包。
            </view>
            <view class="paragraph">
